@charset "UTF-8";
.home {
  background-color: #f7f357; }
  .home .container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    min-height: 100vh; }
    .home .container .text h1 {
      font-size: 50px;
      color: #1a1a17;
      font-family: "Chilanka", cursive; }
    .home .container .text p {
      font-size: 16px; }
    .home .container .text2 {
      text-align: center;
      width: 60vh;
      border-left: 2px solid black; }
      .home .container .text2 h1::before {
        content: "";
        animation: gerak infinite 5s; }
      .home .container .text2 h1 {
        font-size: 60px;
        color: #1a1a17;
        font-family: "Chilanka", cursive; }

@keyframes gerak {
  0% {
    content: "Halo"; }
  25% {
    content: "여보세요"; }
  50% {
    content: "光环"; }
  75% {
    content: "こんにちは"; }
  100% {
    content: "aureola"; } }

@media screen and (max-width: 411px) {
  .home {
    background-color: #f7f357; }
    .home .container {
      display: flex;
      flex-direction: column-reverse;
      transform: translateY(-10vh);
      min-height: 100vh;
      width: 100%; }
      .home .container .text h1 {
        font-size: 30px;
        color: #1a1a17;
        font-family: "Chilanka", cursive; }
      .home .container .text p {
        font-size: 16px; }
      .home .container .text2 {
        text-align: center;
        width: 40vh;
        transform: translateY(18vh);
        border: none; }
        .home .container .text2 h1::before {
          content: "";
          animation: gerak infinite 5s; }
        .home .container .text2 h1 {
          font-size: 60px;
          color: #1a1a17;
          font-family: "Chilanka", cursive; }
  @keyframes gerak {
    0% {
      content: "Halo"; }
    25% {
      content: "여보세요"; }
    50% {
      content: "光环"; }
    75% {
      content: "こんにちは"; }
    100% {
      content: "aureola"; } } }
